/* Global variables */
/* Classes of no value
.fj-fixsize: Fix icon size unless chrome browser.
*/
.ant-menu-dark,
.ant-menu-dark .ant-menu-sub {
  background: transparent !important;
}
.ant-menu.ant-menu-dark .ant-menu-item-selected,
.ant-menu-submenu-popup.ant-menu-dark .ant-menu-item-selected {
  background: #1E1E2C  !important;
  color: #7599E9  !important;
}
.ant-menu.ant-menu-dark .ant-menu-item-selected:before,
.ant-menu-submenu-popup.ant-menu-dark .ant-menu-item-selected:before {
  content: '';
  width: 2px;
  height: 60px;
  background: #7599E9;
  position: absolute;
  left: 0;
  -webkit-transition: height 0.3s cubic-bezier(0.645, 0.045, 0.355, 1);
  transition: height 0.3s cubic-bezier(0.645, 0.045, 0.355, 1);
}
.ant-menu-item:before {
  content: '';
  width: 0px;
  height: 0px;
  background: #7599E9;
  position: absolute;
  left: 0;
}
.ant-menu-dark.ant-menu-inline .ant-menu-item,
.ant-menu-dark.ant-menu-inline .ant-menu-submenu-title {
  height: 60px;
  font-size: 16px;
  line-height: 60px;
}
body,
html {
  font-size: 16px;
  font-family: "Microsoft YaHei", "Helvetica Neue", Helvetica, Arial, sans-serif;
  min-width: 1100px;
  background-color: #ECECEC !important;
}
.fjb-pagecontainer {
  box-sizing: border-box;
}
.fjb-header {
  height: 70px;
  width: 100%;
  background-image: linear-gradient(-90deg, #5A77D3 2%, #3C4C9C 100%);
  position: fixed;
  top: 0;
  left: 0;
  z-index: 101;
  line-height: 70px;
}
.fjb-body {
  padding-top: 70px;
  padding-left: 200px;
  background-color: #f2f3f5;
  box-sizing: border-box;
}
.fjb-aside {
  padding-top: 70px;
  box-sizing: border-box;
  background-color: #fff;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 100;
  width: 200px;
  height: 100%;
}
.fjb-main {
  padding: 30px;
}
.fjb-content {
  min-height: 800px;
}
.fjb-logo {
  height: 31px;
  margin-top: 20px;
  margin-left: 40px;
  margin-right: 30px;
  float: left;
  font-size: 24px;
  color: #FFFFFF;
  line-height: 24px;
}
.fjb-menu {
  width: 18px;
  height: 18px;
  margin-right: 10px;
  display: inline-block;
  font-style: normal;
  vertical-align: sub;
  text-align: center;
  text-transform: none;
  line-height: 1;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.fjb-menu-contract {
  background-image: url(../images/icon-menu-contract.svg);
}
.fjb-menu-data {
  background-image: url(../images/icon-menu-data.svg);
}
.fjb-menu-history {
  background-image: url(../images/icon-menu-history.svg);
}
.fjb-menu-overview {
  background-image: url(../images/icon-menu-overview.svg);
}
.fjb-menu-user {
  background-image: url(../images/icon-menu-user.svg);
}
.fjb-title {
  font-family: PingFangSC-Regular;
  font-size: 20px;
  color: #242433;
  line-height: 20px;
  border-bottom: solid 1px #E7E7E8;
  padding-bottom: 20px;
}



